import React, { useState, useEffect } from "react";
import Skeleton,{SkeletonTheme} from "react-loading-skeleton";
import 'react-loading-skeleton/dist/skeleton.css'
import { NavLink } from 'react-router-dom'


const Products = () => {
  const [formattedPrice, setFormattedPrice] = useState('');
  const [data, setData] = useState([]);
  const [filter, setFilter] = useState(data);
  const [loading, setLoading] = useState(false);
  let componentMounted = true;

 


  useEffect(() => {
    const getProducts = async () => {
      setLoading(true);
      const response = await fetch(`https://fakestoreapi.com/products`);
      if (componentMounted) {
        setData(await response.clone().json());
        setFilter(await response.json());
        setLoading(false);
        console.log(filter);
      }
      return () => {
        componentMounted = false;
      };
    };
    getProducts();
  }, []);

  const Loading = () => {
    return ( 
    <>
    <div className="col-md-3">
    <SkeletonTheme color="#202020" highlightColor="#444">
        <Skeleton height={350}/>
        </SkeletonTheme>
    </div>
    <div className="col-md-3">
    <SkeletonTheme color="#202020" highlightColor="#444">
        <Skeleton height={350}/>
        </SkeletonTheme>

    </div> 
      <div className="col-md-3">
      <SkeletonTheme color="#202020" highlightColor="#444">
        <Skeleton height={350}/>
        </SkeletonTheme>
    </div>
    <div className="col-md-3">
    <SkeletonTheme color="#202020" highlightColor="#444">
        <Skeleton height={350}/>
        </SkeletonTheme>
    </div>
    </>
    );
  };

  const filterProduct = (cat) => {
    const updatedList = data.filter((x)=>x.category === cat);
    setFilter(updatedList);
  }

  const ShowProduct = () => {
    return (
      <>
        <div className="buttons d-flex justify-content-center py-4">
          <button className="btn btn-outline-dark ms-2" onClick={() => setFilter(data)}>All</button>
          <button className="btn btn-outline-dark ms-2" onClick={() =>filterProduct("men's clothing")}>Men's Colthing</button>
          <button className="btn btn-outline-dark ms-2" onClick={() =>filterProduct("women's clothing")}>Womens's Colthing</button>
          <button className="btn btn-outline-dark ms-2" onClick={() =>filterProduct("jewelery")}>Jewelery</button>
          <button className="btn btn-outline-dark ms-2" onClick={() =>filterProduct("electronics")}>Electronics</button>

        </div>
        {filter.map((product) => {

            return(
                <>
                <div className="col-sm-3 mb-4">
                <div className="card h-100 text-center p-4" key={product.id} >
                   <img src={product.image} className="card-img-top" alt={product.title} height="250px" />
                   <div className="card-body">
                    <h5 className="card-title mb-0">{product.title.substring(0,12)}...</h5>
                    <p className="card-text lead fw-bold">
                    {
              (() => {
                const result = product.price * 75.27;
                const formattedResult = result.toFixed(2);
                setFormattedPrice(formattedResult + ' INR');
              })()
            }
            {formattedPrice}
                    </p>
                    <NavLink to={`/products/${product.id}`} className="btn btn-outline-dark">Buy Now</NavLink>
                       </div>
                      </div>
                </div>
                </>

            )

        
        })}
      </>
    );
  };

  return (
    <div>
      <div className="container my-5 py-5">
        <div className="row">
          <div className="col-sm-12">
            <h1 className="display-6 fw-border text-center">Latest Products</h1>
            <hr />
          </div>
        </div>
        <div className="row justify-content-center">
          {loading ? <Loading /> : <ShowProduct />}
        </div>
      </div>
    </div>
  );
};

export default Products;